    // 
    //
    // 
    //  option :
    //
    // parents  this.selectedOptions  this.candidateOptions 
    //
    // 
    // 1 this.candidateOptions 
    // 2 parents  parents  this.candidateOptions  parents  candidateParents
    // 3 option  this.candidateOptions  candidateOption
    // 4 candidateOption  candidateOption  selected  false 
    // 5 candidateOption  candidateOption  allCount  0 partCount 0
    // 6 candidateParents  allCount  partCount 
