<template>
  <article>
    <h1><code>&lt;veui-tabs&gt;</code></h1>
    <section>
      <h2></h2>
      <p> <code>{{active0}}</code></p>
      <p> <code>{{index0 + 1}}</code></p>
      <veui-tabs ui="large" :active.sync="active0" :index.sync="index0">
        <veui-tab label="" name="answers"/>
        <veui-tab label="" name="articles"/>
        <veui-tab label="" name="shares"/>
      </veui-tabs>

      <veui-tabs :active.sync="active0" :index.sync="index0">
        <veui-tab label="" name="answers"/>
        <veui-tab label="" name="articles"/>
        <veui-tab label="" name="shares"/>
      </veui-tabs>

      <veui-tabs ui="small" :active.sync="active0" :index.sync="index0">
        <veui-tab label="" name="answers"/>
        <veui-tab label="" name="articles"/>
        <veui-tab label="" name="shares" status="success"/>
      </veui-tabs>

      <veui-tabs ui="tiny" :active.sync="active0" :index.sync="index0">
        <veui-tab label="" name="answers" status="info"/>
        <veui-tab label="" name="articles"/>
        <veui-tab label="" name="shares"/>
      </veui-tabs>
    </section>
    <section>
      <h2></h2>
      <veui-tabs ui="block" :active.sync="active0" :index.sync="index0">
        <veui-tab label="" name="answers"/>
        <veui-tab label="" name="articles" status="error"/>
        <veui-tab label="" name="shares"/>
      </veui-tabs>
      <veui-tabs ui="block large" :active.sync="active0" :index.sync="index0">
        <veui-tab label="" name="answers" status="warning"/>
        <veui-tab label="" name="articles"/>
        <veui-tab label="" name="shares"/>
      </veui-tabs>
    </section>
    <section>
      <h2></h2>
      <veui-tabs :index="0">
        <veui-tab
          v-for="n in 30"
          :key="n"
          :label="n === 2
            ? 'This is Tab2 with long long long long text'
            : `Tab${n}`"
        />
      </veui-tabs>
      <veui-tabs ui="block" :index="0">
        <veui-tab
          v-for="n in 30"
          :key="n"
          :label="n === 2
            ? 'This is Tab2 with long long long long text'
            : `Tab${n}`"
        />
      </veui-tabs>
    </section>
    <section>
      <h2></h2>
      <p> <code>{{index1 + 1}}</code></p>
      <veui-tabs :index.sync="index1" >
        <veui-tab label="Tab1"><p>This is Tab1</p></veui-tab>
        <veui-tab label="Tab2" disabled><p>This is Tab2</p></veui-tab>
        <veui-tab label="Tab3"><p>This is Tab3</p></veui-tab>
        <veui-tab label="Tab4"><p>This is Tab4</p></veui-tab>
      </veui-tabs>
      <veui-tabs ui="block" :index.sync="index1" >
        <veui-tab label="Tab1"><p>This is Tab1</p></veui-tab>
        <veui-tab label="Tab2" disabled><p>This is Tab2</p></veui-tab>
        <veui-tab label="Tab3"><p>This is Tab3</p></veui-tab>
        <veui-tab label="Tab4"><p>This is Tab4</p></veui-tab>
      </veui-tabs>
    </section>
    <section>
      <h2></h2>
      <veui-tabs :active="$route.fullPath">
        <veui-tab label="Button" to="/tabs/button"/>
        <veui-tab label="Input" to="input"/>
        <veui-tab label="Progress" to="/tabs/progress"/>
        <veui-tab label=" Progress" to="/progress"/>
        <veui-tab label="" to="/tabs/switch">
          <h3>-------- Tabs  switch  --------</h3>
          <router-view v-if="$route.fullPath === '/tabs/switch'"/>
          <h3>-------- Tabs  switch  --------</h3>
        </veui-tab>
      </veui-tabs>
    </section>
    <section class="inner-ui">
      <h2>1 UI</h2>
      <p> <code>{{index2 != null ? index2 + 1 : ''}}</code></p>
      <veui-tabs
        ui="large"
        :active.sync="active1"
        :index.sync="index2"
        addable
        @add="addTab0" @remove="removeTab0"
        :max="totalTabs0"
      >
        <veui-tab
          removable
          :label="tab.label"
          :name="tab.name"
          :status="tab.status"
          :key="tab.name"
          v-for="tab in tabs0"><p>Tab {{ tab.name }}</p>
        </veui-tab>
      </veui-tabs>
      <p> <code>{{index3 != null ? index3 + 1 : ''}}</code></p>
      <veui-tabs ui="large block"
        class="large-block-demo"
        :active.sync="active2"
        :index.sync="index3"
        addable
        @add="addTab1"
        @remove="removeTab1"
        :max="totalTabs1"
        :tip="` ${totalTabs1 - tabs1.length} `"
      >
        <veui-tab
          removable
          :label="tab.label"
          :name="tab.name"
          :status="tab.status"
          :key="tab.name"
          v-for="tab in tabs1"><p>Tab {{ tab.name }}</p>
        </veui-tab>
      </veui-tabs>
    </section>
    <section>
      <h2>2</h2>
      <p> <code>{{index4 != null ? index4 + 1 : ''}}</code></p>
      <veui-button class="add-btn" @click="addTab2"> TAB</veui-button>
      <veui-tabs :active.sync="active3" :index.sync="index4">
        <template v-if="props.removable && tabs2.length > 1" slot="tab-item-extra" slot-scope="props">
          <button type="button" class="veui-tabs-item-remove" @click="removeTab2(props)">
            <icon name="cross-small"/>
          </button>
        </template>
        <veui-tab
          removable
          :label="tab.label"
          :name="tab.name"
          :status="tab.status"
          :key="tab.name"
          v-for="tab in tabs2"><p>Tab {{ tab.name }}</p>
        </veui-tab>
      </veui-tabs>
      <p> <code>{{index5 != null ? index5 + 1 : ''}}</code></p>
      <veui-button class="add-btn" @click="addTab3"> TAB</veui-button>
      <veui-tabs :active.sync="active4" :index.sync="index5" ui="block">
        <template slot="tab-item-extra" slot-scope="props">
          <button v-if="props.removable && tabs3.length > 1"
            type="button"
            class="veui-tabs-item-remove"
            @click="removeTab3(props)">
            <icon name="cross-small"/>
          </button>
        </template>
        <veui-tab
          removable
          :label="tab.label"
          :name="tab.name"
          :status="tab.status"
          :key="tab.name"
          v-for="tab in tabs3">
          <p v-if="tab.label ===''">
            
            <br v-for="n in 10" :key="n">
          </p>
          <p v-else>Tab {{ tab.name }}</p>
        </veui-tab>
      </veui-tabs>
    </section>
    <section>
      <h2>3v-if  dom </h2>
      <p> <code>{{active5}}</code></p>
      <p> <code>{{index6 + 1}}</code></p>
      <veui-button
        :disabled="tabIfRemoving"
        @click="insertVisiable = !insertVisiable"
      >{{ insertVisiable ? '' : '' }} TAB</veui-button>
      <veui-tabs ui="large" :active.sync="active5" :index.sync="index6">
        <veui-tab label="DuerOS" name="os"><p>os</p></veui-tab>
        <veui-tab label="" name="car"><p>car</p></veui-tab>
        <veui-tab label="" name="face" v-if="insertVisiable"><p>face</p></veui-tab>
        <veui-tab label="" name="sound"><p>sound</p></veui-tab>
      </veui-tabs>
      <veui-tabs ui="large block" :active.sync="active5" :index.sync="index6">
        <veui-tab label="DuerOS" name="os"><p>os</p></veui-tab>
        <veui-tab label="" name="car"><p>car</p></veui-tab>
        <veui-tab label="" name="face" v-if="insertVisiable"><p>face</p></veui-tab>
        <veui-tab label="" name="sound"><p>sound</p></veui-tab>
      </veui-tabs>
    </section>
  </article>
</template>

<script>
import { Icon, Tabs, Tab, Button } from 'veui';
import { findIndex, uniqueId } from 'lodash';
export default {
    name: 'tabs-demo',
    components: {
        'veui-tabs': Tabs,
        'veui-tab': Tab,
        'veui-button': Button,
        'icon': Icon
    },
    data() {
        return {
          totalTabs0: 10,
          totalTabs1: 20,
          tabs0: [
        {label: '', name: '1'},
        {label: '2', name: '2', status: 'success'},
        {label: '3', name: '3'}
        ],
          tabs1: [
        {label: '1', name: '1'},
        {label: '', name: '2', status: 'info'},
        {label: '3', name: '3'}
        ],
          tabs2: [
        {label: '1', name: '1'},
        {label: '', name: '2', status: 'warning'},
        {label: '3', name: '3'}
        ],
          tabs3: [
        {label: '1', name: '1', status: 'error'},
        {label: '', name: '2', status: 'warning'},
        {label: '3', name: '3'}
        ],
          active0: '',
          active1: '',
          active2: '',
          active3: '',
          active4: '',
          active5: '',
          index0: 0,
          index1: 0,
          index2: 0,
          index3: 0,
          index4: 0,
          index5: 0,
          index6: 0,
          insertVisiable: false,
          tabIfRemoving: false
      };
    },
    methods: {
        addTab0() {
          if (this.tabs0.length >= this.totalTabs0) {
            return;
        }
          let label = uniqueId('');
          let index = this.tabs0.push({
            label,
            name: label
        });
          this.index2 = index - 1;
      },
        addTab1() {
          if (this.tabs1.length >= this.totalTabs1) {
            return;
        }
          let label = uniqueId('');
          let index = this.tabs1.push({
            label,
            name: label
        });
          this.index3 = index - 1;
      },
        addTab2() {
          let label = uniqueId('');
          let index = this.tabs2.push({
            label,
            name: label
        });
          this.index4 = index - 1;
      },
        addTab3() {
          let label = uniqueId('');
          let index = this.tabs3.push({
            label,
            name: label
        });
          this.index5 = index - 1;
      },
        removeTab0({name}) {
          let index = findIndex(this.tabs0, tab => tab.name === name);
          if (index !== -1) {
            this.tabs0.splice(index, 1);
        }
      },
        removeTab1({name}) {
          let index = findIndex(this.tabs1, tab => tab.name === name);
          if (index !== -1) {
            this.tabs1.splice(index, 1);
        }
      },
        removeTab2({name}) {
          let index = findIndex(this.tabs2, tab => tab.name === name);
          if (index !== -1) {
            this.tabs2.splice(index, 1);
        }
      },
        removeTab3({name}) {
          let index = findIndex(this.tabs3, tab => tab.name === name);
          if (index !== -1) {
            this.tabs3.splice(index, 1);
        }
      }
    }
};
</script>

<style lang="less" scoped>
section + section {
  margin-top: 60px;
}
.veui-tabs {
  & + &[ui~="block"] {
    margin-top: 20px;
  }
}
.large-block-demo .veui-tab {
  border: 1px solid #e7e7e7;
  margin-top: -1px;
  p {
    padding: 0 30px;
  }
}
.add-btn {
  margin-bottom: 10px;
}
</style>


