<template>
  <article>
    <h1><code>&lt;veui-alert&gt;</code></h1>
    <section>
      <div></div>
      <veui-alert
        type="success"
        message=""
        closable/>
      <veui-alert
        ui="limit"
        type="success"
        message=""
        closable
        close-label=""/>
      <veui-alert
        type="warning"
        message=""
        closable/>
      <veui-alert
        ui="limit"
        type="warning"
        :message="messages"
        closable/>
      <veui-alert
        type="info"
        message=""
        closable/>
      <veui-alert
        ui="limit"
        type="info"
        message=""
        closable
        close-label=""/>
      <veui-alert
        type="error"
        message=""
        closable/>
      <veui-alert
        ui="limit"
        type="error"
        message=""
        closable
        close-label=""/>
    </section>
  </article>
</template>

<script>
import bus from '../bus';
import { Alert } from 'veui';

export default {
    name: 'alert',
    components: {
        'veui-alert': Alert
    },
    data() {
        return {
            open: false,
            messages: [
                '1111',
                '2',
                '3',
                '4',
                '5'
            ],
            messageIndex: 0
        };
    },
    mounted() {
        this.$children.forEach(child => {
            child.$on('click', () => {
                bus.$emit('log', child.$el.getAttribute('ui'));
            });
        });
    },
    methods: {
        close() {
            this.open = true;
        }
    }
};
</script>

<style scoped>
section {
  margin: 30px;
}

.veui-alert {
  margin: 30px 0;
}
</style>



