<template>
  <article>
    <h1><code>&lt;veui-checkbox&gt;</code></h1>
    <p>
      <veui-checkbox v-model="current" true-value="" false-value=""></veui-checkbox><span class="selected-val">{{ current }}</span>
    </p>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="small"(12px)</span>
      </p>
      <p>
        <veui-checkbox v-model="picked13" ui="small"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="small" v-model="picked14"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="small" indeterminate v-model="picked15"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="small" disabled v-model="picked16"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="small" disabled v-model="picked17"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="small" disabled indeterminate v-model="picked18"></veui-checkbox>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui(14px)</span>
      </p>
      <p>
        <veui-checkbox v-model="picked1"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox v-model="picked3"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox indeterminate v-model="picked5"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox disabled v-model="picked7"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox disabled v-model="picked9"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox disabled indeterminate v-model="picked11"></veui-checkbox>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="large"(16px)</span>
      </p>
      <p>
        <veui-checkbox ui="large" v-model="picked2"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="large" v-model="picked4"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="large" indeterminate v-model="picked6"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="large" disabled v-model="picked8"></veui-checkbox>
      </p>
      
      <p>
        <veui-checkbox ui="large" disabled v-model="picked10"></veui-checkbox>
      </p>
      <p>
        <veui-checkbox ui="large" disabled indeterminate v-model="picked12"></veui-checkbox>
      </p>
    </section>
    <section>
      <h2></h2>
      <p v-text="sample" class="sample"></p>
    </section>
    <section class="attrTab">
      <h2>Attributes</h2>
      <veui-table :data="attrData">
        <veui-table-column field="parameter" title="" width="10%"></veui-table-column>
        <veui-table-column field="desc" title="" width="40%"></veui-table-column>
        <veui-table-column field="type" title="" width="10%"></veui-table-column>
        <veui-table-column field="canSelectVal" title="" width="30%"></veui-table-column>
        <veui-table-column field="defaultVal" title="" width="10%"></veui-table-column>
      </veui-table>
    </section>
  </article>
</template>

<script>
import { Checkbox, Table, Column } from 'veui'

export default {
  name: 'checkbox-demo',
  components: {
    'veui-checkbox': Checkbox,
    'veui-table': Table,
    'veui-table-column': Column
  },
  data () {
    return {
      current: '',
      picked1: false,
      picked2: false,
      picked3: true,
      picked4: true,
      picked5: false,
      picked6: false,
      picked7: true,
      picked8: true,
      picked9: false,
      picked10: false,
      picked11: false,
      picked12: false,
      picked13: false,
      picked14: true,
      picked15: false,
      picked16: true,
      picked17: false,
      picked18: false,
      sample: '<veui-checkbox ui="small" v-model="current" true-value="" false-value=""></veui-checkbox>',
      attrData: [
        {
          parameter: 'ui', desc: '', type: 'string', canSelectVal: 'small,large', defaultVal: '--'
        },
        {
          parameter: 'true-value', desc: '', type: 'string,number', canSelectVal: '--', defaultVal: 'true'
        },
        {
          parameter: 'false-value', desc: '', type: 'string,number', canSelectVal: '--', defaultVal: 'false'
        },
        {
          parameter: 'checked', desc: '', type: 'boolean', canSelectVal: 'true,false', defaultVal: 'false'
        },
        {
          parameter: 'indeterminate', desc: ' indeterminate ()', type: 'boolean', canSelectVal: 'true,false', defaultVal: 'false'
        }
      ]
    }
  }
}
</script>
<style lang="less" scoped>
section {
  margin-bottom: 20px;
}
.veui-checkbox {
  vertical-align: middle;
}
.selected-val {
  height: 14px;
  line-height: 14px;
  display: inline-block;
  vertical-align: middle;
}
</style>




