<template>
  <article>
    <h1><code>&lt;veui-dropdown&gt;</code></h1>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui=""</span>
      </p>
      <p>
        <span class="veui-font-level-2d">split</span>
      </p>
      <p>
        <veui-dropdown
        label=""
        split
        :options="options"></veui-dropdown>
      </p>
      <p>
        <span class="veui-font-level-2d">ui="large"</span>
      </p>
      <p>
        <veui-dropdown
        ui="large"
        label=""
        :options="options"></veui-dropdown>
      </p>
      <p>
        <span class="veui-font-level-2d">ui=""</span>
      </p>
      <p>
        <veui-dropdown
        label=""
        :options="options"></veui-dropdown>
      </p>
      <p>
        <span class="veui-font-level-2d">ui="small"</span>
      </p>
      <p>
        <veui-dropdown
        ui="small"
        label=""
        :options="options">
          <template slot="option-label" slot-scope="{ label }">
             {{ label }}
          </template>
        </veui-dropdown>
      </p>
      <p>
        <veui-dropdown
          label=""
          disabled
          :options="options"></veui-dropdown>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="link"</span>
      </p>
      <p>
        <veui-dropdown
          ui="link"
          label="">
          <veui-option-group label="">
            <veui-option @click="log('save')" label=""/>
            <veui-option value="publish" label=""/>
          </veui-option-group>
          <veui-option-group label="">
            <veui-option value="undo" label=""/>
            <veui-option value="redo" label=""/>
          </veui-option-group>
        </veui-dropdown>
      </p>
      <p>
        <veui-dropdown
          ui="link"
          label=""
          disabled
          :options="options"></veui-dropdown>
      </p>
    </section>
  </article>
</template>

<script>
import bus from '../bus';
import { Dropdown, OptionGroup, Option } from 'veui';

export default {
    name: 'dropdown-demo',
    components: {
        'veui-dropdown': Dropdown,
        'veui-option-group': OptionGroup,
        'veui-option': Option
    },
    data() {
        return {
            options: [
                {
                    label: '',
                    value: 'create'
                },
                {
                    label: '',
                    value: 'edit',
                    disabled: true
                },
                {
                    label: '',
                    value: 'remove'
                }
            ]
        };
    },
    mounted() {
        this.$children.forEach(child => {
            child.$on('click', val => {
                bus.$emit('log', val);
            });
        });
    },
    methods: {
        log(val) {
            bus.$emit('log', val);
        }
    }
};
</script>

<style lang="less" scoped>
p {
  margin: 10px 0;
}

</style>



