<template>
  <article>
    <h1><code>&lt;veui-pagination&gt;</code></h1>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui</span>
      </p>
      <p><veui-pagination :page="page" :total="total" :to="to"></veui-pagination></p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="info"(ui=info)</span>
      </p>
      <p><veui-pagination :page="page" ui="info" :total="total" :to="to"></veui-pagination></p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="hetero"</span>
      </p>
      <p><veui-pagination :page="page" :total="total" :to="to" ui="hetero full1"></veui-pagination></p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="full"ui="hetero info"</span>
      </p>
      <p class="veui-font-level-2d">ui="full"</p>
      <p><veui-pagination :page="page" :total="total" :page-sizes="pageSizes" :to="to" :page-size.sync="pageSize" ui="full"></veui-pagination></p>
      <p class="veui-font-level-2d">ui="hetero info"</p>
      <p><veui-pagination :page="page" :total="total" :page-sizes="pageSizes" :to="to" :page-size.sync="pageSize" ui="hetero info"></veui-pagination></p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="square"</span>
      </p>
      <p><veui-pagination :page="page" :total="total" :to="to" ui="square"></veui-pagination></p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="simple"</span>
      </p>
      <p><veui-pagination :page="page" :total="total" :to="to" ui="simple"></veui-pagination></p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="big_square"</span>
      </p>
      <p><veui-pagination :page="page" :total="total" :to="to" ui="big_square"></veui-pagination></p>
    </section>

    <section>
      <h2></h2>
      <p><small> &lt;router-link&gt;  to prop </small></p>
      <p><veui-pagination :page="page" :total="total" ui="advanced"
        :to="{name: 'Pagination', params: { page: ':page'}}"></veui-pagination></p>
    </section>

    <section>
      <h2></h2>
      <p><veui-pagination :page="page" :total="total" :to="'#' + to" ui="advanced" :native="true"></veui-pagination></p>
    </section>

    <section>
      <h2></h2>
      <p><small></small></p>
      <p><veui-pagination :page="page" :total="total" :to="to" ui="advanced" :native="true"
        @redirect="handlePageRedirect"></veui-pagination></p>
      <div class="message">{{ fifthPaginationMessage }}</div>
    </section>

    <section>
      <h2></h2>
      <p><veui-pagination :page="1" :total="0" :to="'#' + to" ui="full" :native="true"></veui-pagination></p>
    </section>

    <section>
      <h2></h2>
      <p v-text="sample" class="sample"></p>
    </section>

    <section class="attrTab">
      <h2>Attributes</h2>
      <veui-table :data="attrData">
        <veui-table-column field="parameter" title="" width="10%"></veui-table-column>
        <veui-table-column field="desc" title="" width="40%"></veui-table-column>
        <veui-table-column field="type" title="" width="10%"></veui-table-column>
        <veui-table-column field="canSelectVal" title="" width="30%"></veui-table-column>
        <veui-table-column field="defaultVal" title="" width="10%"></veui-table-column>
      </veui-table>
    </section>
  </article>
</template>

<script>
import bus from '../bus';
import { Pagination, Table, Column } from 'veui';

export default {
    name: 'pagination-demo',
    components: {
        'veui-pagination': Pagination,
        'veui-table': Table,
        'veui-table-column': Column
    },
    data() {
        return {
            page: parseInt(this.$route.params.page, 10) || 1,
            total: 5000,
            to: '/pagination/:page',
            pageSize: 30,
            pageSizes: [30, 60, 100, 200],
            fifthPaginationMessage: '',
            sample: '<veui-pagination :page="1" :page-size="20" :total="100" :page-sizes="[10, 20, 30]" :to="" ui="full" :native="true"></veui-pagination>',
            attrData: [
                {
                    parameter: 'page', desc: '', type: 'number', canSelectVal: '--', defaultVal: '1'
                },
                {
                    parameter: 'page-size', desc: '', type: 'number', canSelectVal: '--', defaultVal: '30'
                },
                {
                    parameter: 'page-sizes', desc: '', type: 'array', canSelectVal: '--', defaultVal: '[30, 50, 100]'
                },
                {
                    parameter: 'total', desc: '', type: 'number', canSelectVal: '--', defaultVal: '--'
                },
                {
                    parameter: 'ui', desc: '', type: 'string', canSelectVal: 'heterofullsquaresimplebig_square', defaultVal: '--'
                },
                {
                    parameter: 'native', desc: '', type: 'boolean', canSelectVal: '', defaultVal: 'falsetruefalseto'
                }
            ]
        };
    },
    methods: {
        handlePageRedirect({page, event}) {
            event.preventDefault();
            this.fifthPaginationMessage = `${page}`;
        }
    },
    mounted() {
        this.$children.forEach(child => {
            child.$on('click', () => {
                bus.$emit('log', child.$el.getAttribute('ui'));
            });
        });
    },
    beforeRouteUpdate({params}, from, next) {
        let page = parseInt(params.page, 10);
        this.page = isNaN(page) ? 1 : page;
        next();
    }
};
</script>

<style lang="less" scoped>
section {
  margin-bottom: 3em;
}
.veui-pagination {
  margin: 1em 0 3em;
}

.message {
  margin-top: -3em;
}
</style>



