<template>
  <article>
    <h1><code>&lt;veui-select&gt;</code></h1>
    <section>
      <h2></h2>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="tiny"</span>
      </p>
      <p>
        <veui-select v-bind="attrs" v-model="defaultValue1" placeholder="" ui="tiny" clearable :filter="filter">
          <div slot="before"><veui-input class="filter" ui="tiny" v-model="keyword"/></div>
        </veui-select>
      </p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="small"</span>
      </p>
      <p>
        <veui-select v-bind="attrs" v-model="defaultValue1" placeholder="" ui="small" clearable></veui-select>
      </p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="normal"</span>
      </p>
      <p>
        <veui-select v-bind="attrs" v-model="defaultValue1" placeholder="" clearable></veui-select>
      </p>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="large"</span>
      </p>
      <p>
        <veui-select v-bind="attrs" v-model="defaultValue1" placeholder="" ui="large" clearable></veui-select>
      </p>
    </section>
    <section>
      <p>
        <span class="veui-font-level-1b"></span>
        <span class="veui-font-level-2d">ui="checkmark"</span>
      </p>
      <veui-select v-bind="attrs" ui="checkmark" v-model="defaultValue2"></veui-select>
    </section>
    <section>
      <h2></h2>
      <veui-select v-bind="attrs" v-model="defaultValue1" disabled></veui-select>
    </section>
    <section>
      <h2>Slot[name=option-label] </h2>
      <veui-select v-bind="attrs" v-model="defaultValue3">
        <template slot="option-label" slot-scope="props">
          <span class="veui-option-custom-label">{{ props.label }}</span>
        </template>
      </veui-select>
    </section>
    <section>
      <h2>Slot  2</h2>
      <veui-select v-bind="attrs" v-model="defaultValue4">
        <template slot="option" slot-scope="props">
          <span class="veui-option-label-text">{{ props.label }}</span>
          <veui-icon name="eye"/>
        </template>
      </veui-select>
    </section>
    <section>
      <h2>Slot  3</h2>
      <veui-select v-bind="attrs" v-model="defaultValue1">
        <template slot="option" slot-scope="props">
          <veui-radio :checked="props.selected">{{ props.label }}</veui-radio>
        </template>
      </veui-select>
    </section>
    <section>
      <h2></h2>
      <veui-select v-model="hero">
        <veui-option-group label="" position="popup">
          <veui-option-group label="" position="popup">
            <veui-option value="baidu" label=""/>
            <veui-option value="alibaba" label=""/>
            <veui-option value="tencent" label=""/>
          </veui-option-group>
          <veui-option-group label="" position="popup">
            <veui-option value="telecom" label=""/>
            <veui-option value="mobile" label=""/>
          </veui-option-group>
        </veui-option-group>
        <veui-option-group label="" position="popup">
          <veui-option-group label="" position="popup">
            <veui-option value="google" label="Google"/>
            <veui-option value="facebook" label="Facebook"/>
            <veui-option value="twitter" label="Twitter"/>
          </veui-option-group>
          <veui-option-group label="" position="popup">
            <veui-option value="atnt" label="AT&T"/>
            <veui-option value="verizon" label="Verizon"/>
          </veui-option-group>
        </veui-option-group>
      </veui-select>
    </section>
    <section>
      <h2> 2</h2>
      <veui-select v-model="phone">
        <veui-option-group label="">
          <veui-option value="xiaomi" label=""/>
          <veui-option value="oppo" label="OPPO"/>
          <veui-option value="vivo" label="vivo"/>
          <veui-option value="smartisan" label=""/>
        </veui-option-group>
        <veui-option-group label="" position="popup">
          <veui-option-group label="" position="popup">
            <veui-option value="apple" label="Apple"/>
            <veui-option value="google" label="Google"/>
          </veui-option-group>
          <veui-option-group label="" position="popup">
            <veui-option value="sharp" label=""/>
            <veui-option value="sony" label=""/>
          </veui-option-group>
          <veui-option-group label="" position="popup">
            <veui-option value="samsung" label=""/>
            <veui-option value="lg" label="LG"/>
          </veui-option-group>
        </veui-option-group>
      </veui-select>
    </section>
    <section style="margin-top:200px;">
      <h2></h2>
      <veui-select v-bind="optGroupAttrs" v-model="defaultValue5"></veui-select>
    </section>
    <section>
      <h2></h2>
      <veui-select v-bind="optGroupAttrs" ui="checkmark" v-model="defaultValue6"></veui-select>
    </section>
    <section>
      <h2>Slot  1</h2>
      <veui-select v-bind="optGroupAttrs" v-model="defaultValue7">
        <template slot="option" slot-scope="props">
          <div class="veui-option-custom">{{ props.label }}</div>
        </template>
      </veui-select>
    </section>
    <section>
      <h2>Slot[name=option]  2</h2>
      <veui-select v-bind="optGroupAttrs" v-model="defaultValue8" :overlay-options="{
          position: 'bottom right'
        }">
        <template slot="option" slot-scope="props">
          <span class="veui-option-label-text veui-option-custom-label">{{ props.label }}</span>
          <veui-icon name="gift"/>
        </template>
      </veui-select>
    </section>
    <section>
      <h2></h2>
      <veui-select v-bind="optGroupAttrs" :options="groupedOpts" v-model="defaultValue9"></veui-select>
    </section>
    <section>
      <h2> 3</h2>
      <veui-select v-model="defaultValue10" clearable>
        <veui-option-group label="">
          <veui-option value="A" label=" A"/>
          <veui-option value="B" label=" B"/>
          <veui-option value="C" label=" C"/>
        </veui-option-group>
        <veui-option-group label="">
          <veui-option value="1" label=" 1"/>
          <veui-option value="2" label=" 2"/>
          <veui-option value="3" label=" 3"/>
        </veui-option-group>
      </veui-select>
    </section>
  </article>
</template>

<script>
import bus from '../bus'
import { Icon, Select, Option, OptionGroup, Radio, Input } from 'veui'
import type from 'veui/managers/type'
import 'vue-awesome/icons/eye'
import 'vue-awesome/icons/gift'
import 'veui-theme-blue/icons/angle-right'
export default {
  name: 'select-demo',
  components: {
    'veui-select': Select,
    'veui-option': Option,
    'veui-option-group': OptionGroup,
    'veui-radio': Radio,
    'veui-icon': Icon,
    'veui-input': Input
  },
  data () {
    return {
      filter: ({ label }) => {
        return label.indexOf(this.keyword) !== -1
      },
      keyword: '',
      phone: null,
      hero: null,
      disabled: true,
      selected: true,
      icon: true,
      defaultValue1: null,
      defaultValue2: null,
      defaultValue3: null,
      defaultValue4: null,
      defaultValue5: null,
      defaultValue6: null,
      defaultValue7: null,
      defaultValue8: '1-1',
      defaultValue9: null,
      defaultValue10: null,
      defaultValue11: null,
      attrs: {
        name: 'age',
        readonly: false,
        disabled: false,
        clearable: true,
        options: [
          {
            label: '1',
            value: 1
          },
          {
            label: '22',
            value: 2
          },
          {
            label: '3',
            value: 3
          },
          {
            label: '4',
            value: 4
          }
        ]
      },
      optGroupAttrs1: {
        ui: 'multilayer',
        name: 'name',
        readonly: false,
        disabled: false,
        selecticon: false,
        clearable: true,
        options: [
          {
            label: '1',
            children: [
              {
                label: '1-1',
                value: 'd-1-1'
              },
              {
                label: '1-2',
                value: 'd-1-2'
              },
              {
                label: '1-3',
                value: 'd-1-3'
              }
            ]
          },
          {
            label: '2',
            children: [
              {
                label: '2-1',
                value: 'd-2-1'
              },
              {
                label: '2-2',
                value: 'd-2-2'
              },
              {
                label: '2-3',
                value: 'd-2-3'
              }
            ]
          },
          {
            label: '3',
            children: [
              {
                label: '3-1',
                value: 'd-3-1'
              },
              {
                label: '3-2',
                value: 'd-3-2'
              },
              {
                label: '3-3',
                value: 'd-3-3'
              }
            ]
          },
          {
            label: '4',
            children: [
              {
                label: '4-1',
                value: 'd-4-1'
              },
              {
                label: '4-2',
                value: 'd-4-2'
              },
              {
                label: '4-3',
                value: 'd-4-3'
              }
            ]
          }
        ]
      },
      optGroupAttrs: {
        ui: 'aux',
        name: 'name',
        readonly: false,
        disabled: false,
        clearable: true,
        options: [
          {
            label: '1',
            position: 'popout',
            options: [
              {
                label: '1',
                value: '1-1'
              },
              {
                label: '2',
                value: '1-2'
              },
              {
                label: '3',
                value: '1-3'
              }
            ]
          },
          {
            label: '2',
            options: [
              {
                label: '1',
                value: '2-1'
              },
              {
                label: '2',
                value: '2-2'
              },
              {
                label: '3',
                value: '2-3'
              }
            ]
          },
          {
            label: '3',
            options: [
              {
                label: '1',
                value: '3-1'
              },
              {
                label: '2',
                value: '3-2'
              },
              {
                label: '3',
                value: '3-3'
              }
            ]
          },
          {
            label: '4',
            options: [
              {
                label: '1',
                value: '4-1'
              },
              {
                label: '2',
                value: '4-2'
              },
              {
                label: '3',
                value: '4-3'
              }
            ]
          }
        ]
      }
    }
  },
  computed: {
    groupedOpts () {
      return type.clone(this.optGroupAttrs.options).map(group => {
        delete group.label
        return group
      }).reduce((acc, cur) => {
        acc.push(cur)
        return acc
      }, [])
    }
  },
  mounted () {
    this.$children.forEach(child => {
      child.$on('click', () => {
        bus.$emit('log', child.$el.getAttribute('ui'))
      })
    })
  },
  methods: {
    change (value) {
      // alert('' + value)
    }
  }
}
</script>

<style lang="less" scoped>
.veui-option-label {
  &-text,
  .veui-icon {
    vertical-align: middle;
  }
  .veui-icon {
    margin-left: 5px;
  }
}
.veui-option-custom {
  position: relative;
  padding-left: 15px;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  &-label {
    color: #666666;
  }
}
.filter {
  width: ~"calc(100% - 10px)";
  margin: 5px;
}
</style>


